/* Auto Trader Role Selector */
.role-selector-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    margin-bottom: 24px;
}

.role-card {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid var(--glass-border);
    border-radius: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.role-card:hover {
    background: rgba(255, 255, 255, 0.04);
    border-color: rgba(255, 255, 255, 0.15);
}

.role-card.active {
    background: rgba(41, 151, 255, 0.1);
    border-color: var(--accent);
    box-shadow: 0 0 20px rgba(41, 151, 255, 0.1);
}

.role-icon {
    width: 44px;
    height: 44px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--secondary);
    transition: all 0.3s ease;
}

.role-card.active .role-icon {
    background: var(--accent);
    color: #fff;
}

.role-info h4 {
    font-size: 14px;
    font-weight: 800;
    margin: 0 0 4px 0;
}

.role-info p {
    font-size: 11px;
    color: var(--secondary);
    line-height: 1.3;
    margin: 0;
}

/* Mesh Toggle Grid */
.mesh-toggle-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
}

.mesh-toggle-card {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 14px;
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid var(--glass-border);
    border-radius: 10px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 700;
    color: var(--secondary);
    transition: all 0.2s ease;
}

.mesh-toggle-card:hover {
    background: rgba(255, 255, 255, 0.04);
}

.mesh-toggle-card.active {
    background: rgba(41, 151, 255, 0.1);
    border-color: var(--accent);
    color: var(--accent);
}

.mesh-field.full-width {
    grid-column: span 2;
}

.risk-value-input .input-wrapper {
    background: rgba(255, 255, 255, 0.05);
}